.lista-pacientes::-webkit-scrollbar {
    width: 3px; /* ancho de la barra */
}

.lista-pacientes::-webkit-scrollbar-track {
    background: #FFFFFF; /* color del fondo de la barra de desplazamiento */
}

.lista-pacientes::-webkit-scrollbar-thumb {
    background: #007EA7; /* color del pulgar */
}