:root {
  --text-family: "Verdana", san-serif;
}

* {
  font-family: var(--text-family);
  box-sizing: border-box;
  user-select: none;
}

html,
body {
  height: 100%;
  width: 100%;
  margin: 0;
  padding: 0;
}

#root {
  width: 100%;
  height: 100%;
}


.App {
  width: 100%;
  height: 100%;
}